.footer {
  width: 100%;
  height: 80px;
  background: #2d333b;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
}

.socialMedia svg {
  color: white;
  margin: 20px;
  font-size: 50px;
  cursor: pointer;
}

.footer p {
  color: white;
}

@media only screen and (max-width: 600px) {
  .footer {
    padding-top: 0px;
  }
  .footer svg {
    font-size: 40px;
  }
}
